
<template>
  <v-container>
    <dashboard-date-picker v-model="dates"></dashboard-date-picker>
    
    <app-total :dates="dates" v-if="!isWakel "></app-total>
     <agent-target :dates="dates" v-if="!isWakel && !isAffiliate"></agent-target>
 


    <!-- admin section -->
    <v-row v-if="isAdmin">
      <total-sheet v-for="business in businesses" :key="business._id" :title="business.name" :id="business._id" :dates="dates"></total-sheet>
    </v-row>
    <!-- manager section -->
     

    <!-- START Team Leader Section  -->
    <!-- <agent-upsales v-if="isAgent" :dates="dates"></agent-upsales> -->
    <!-- <v-divider v-if="isAgent" class="my-4"></v-divider> -->
    <team-leader-report v-if="isTeamLeader" :dates="dates"></team-leader-report>
    <!-- END Team Leader Section  -->

    <!-- START Agent Section  -->
    <agent-upsales v-if="isAgent" :dates="dates"></agent-upsales>
    <v-divider v-if="isAgent" class="my-4"></v-divider>
    <agent-report v-if="isAgent" :dates="dates"></agent-report>
    <!-- END Agent Section  -->

    <!-- AFFILIATE SECTION -->
    <affiliate-deposits :dates="dates" v-if="isAffiliate"></affiliate-deposits>
    <v-divider class="my-6" v-if="isAffiliate"></v-divider>
    <affiliate-upsales :dates="dates" v-if="isAffiliate" ></affiliate-upsales>
    <!-- END -->



    <!-- START Wakel -->
    <wakel-total :dates="dates" v-if="isWakel"></wakel-total>
    <!-- END Wakel -->



    <!-- START wakels total for admin -->
    <v-divider v-if="isAdmin" class="my-6"> </v-divider>
 
    <wakels-transactions :dates="dates" v-if="isAdmin"></wakels-transactions>
    <!-- END wakels total for admin -->
    
  
  </v-container>
</template>
<script>

 
// import 
import DashboardDatePicker from './../components/dashboard/DatePicker'



import AppTotal from './../components/dashboard/Total'
import TotalSheet from './../components/dashboard/TotalSheet'

import AffiliateDeposits from './../components/dashboard/AffiliateDeposits'
import AffiliateUpsales from './../components/dashboard/AffiliateUpsales'



import AgentReport from './../components/dashboard/AgentReport'
import AgentUpsales from './../components/dashboard/AgentUpsales'

import TeamLeaderReport from './../components/dashboard/TeamLeaderReport'



import WakelTotal from './../components/dashboard/WakelTotal'
import WakelsTransactions from './../components/dashboard/WakelsTransactions'





import { mapActions, mapState } from 'vuex'
import utility from '../utility'
import AgentTarget from '../components/dashboard/AgentTarget.vue'






export default {
  name: 'Dashboard',
  components: {
    DashboardDatePicker,
    AppTotal,
    TotalSheet,
    AffiliateDeposits,
    AffiliateUpsales,
    AgentReport,
    AgentUpsales,
    AgentTarget,
    TeamLeaderReport,
    WakelTotal,
    WakelsTransactions
     
  },
  data() {
    return {
      dates: utility.currentDateRange, 
      businesses: [],
    }
  },
  computed: {
    ...mapState({ 
      user: state => state.auth.user
    }),
    isAdmin () {
        // is admin or owner
        return this.user.role == 'admin' || this.user.role == 'owner'
    },
    isAgent () {
      return this.user.role == 'agent'
    },
    isTeamLeader () {
      return this.user.role == 'team_leader'
    },
    isAffiliate () {
      return this.user.role == 'affiliate'
    },
    isWakel () {
      return this.user.role == 'wakel'
    } 
  },
  methods: {
    ...mapActions({
      get: 'api/get',
    }),
  },
  async created () {
    if (this.isAdmin) {
      const businesses = await this.get({ collection: 'business', query: {
        sortBy: ['name'], sortDesc: [false]
      } })
      if (businesses && businesses.status == 200 && businesses.data.docs.length)
        this.businesses = businesses.data.docs
        
    }
  }
}
</script>